import { BubbleMenuItems } from './BubbleMenuItems';
import { RichTextEditorContext } from './Context';
import {
  BoldIcon,
  ItalicIcon,
  LinkIcon,
  ListBulletIcon,
  NumberedListIcon,
} from '@heroicons/react/16/solid';
import Bold from '@tiptap/extension-bold';
import BubbleMenuExt from '@tiptap/extension-bubble-menu';
import BulletList from '@tiptap/extension-bullet-list';
import Document from '@tiptap/extension-document';
import DropCursor from '@tiptap/extension-dropcursor';
import Italic from '@tiptap/extension-italic';
import Link from '@tiptap/extension-link';
import ListItem from '@tiptap/extension-list-item';
import OrderedList from '@tiptap/extension-ordered-list';
import Paragraph from '@tiptap/extension-paragraph';
import Text from '@tiptap/extension-text';
import CharacterCount from '@tiptap/extension-character-count';
import {
  BubbleMenu,
  EditorContent,
  useEditor,
  useEditorState,
} from '@tiptap/react';
import { useCallback, useContext } from 'react';

export default function RichTextEditor({ content, limit }: { content?: string; limit?: number }) {
  const context = useContext(RichTextEditorContext);

  let parsedContent;

  if (!limit)
    limit = 500;

  // define your extension array
  const extensions = [
    BulletList.configure({ HTMLAttributes: { class: 'list-disc ml-5' } }),
    // ListItem.configure({ HTMLAttributes: { class: '' } }),
    ListItem,
    OrderedList.configure({
      HTMLAttributes: { class: 'list-decimal ml-5' },
    }),
    Document,
    // Paragraph.configure({ HTMLAttributes: { class: '' } }),
    Paragraph,
    Text,
    Bold,
    Italic,
    Link.configure({
      openOnClick: false,
      autolink: true,
      defaultProtocol: 'https',
    }),
    CharacterCount.configure({
      limit,
    }),
    DropCursor,
    BubbleMenuExt.configure({
      shouldShow: ({ editor }) => {
        // only show the bubble menu for images and links
        return editor.isActive('link');
      },
    }),
  ];

  try {
    if (content)
      parsedContent = JSON.parse(content);
  } catch (e) {
    parsedContent = content;
  }

  const editor = useEditor({
    extensions,
    content: parsedContent,
    editable: true,
    onUpdate({ editor }) {
      if (context.setContent && editor)
        context.setContent(JSON.stringify(editor.getJSON()));
    },
  });

  /**
   * This hook allows us to select the editor state we want to use in our component.
   */
  const currentEditorState = useEditorState({
    /**
     * The editor instance we want to use.
     */
    editor,
    /**
     * This selector allows us to select the data we want to use in our component.
     * It is evaluated on every editor transaction and compared to it's previously returned value.
     */
    selector: (ctx) => ({
      isBold: ctx.editor?.isActive('bold') ?? false,
      isItalic: ctx.editor?.isActive('italic') ?? false,
      isStrike: ctx.editor?.isActive('strike') ?? false,
      isBulletList: ctx.editor?.isActive('bulletList') ?? false,
      isOrderedList: ctx.editor?.isActive('orderedList') ?? false,
      isLink: ctx.editor?.isActive('link') ?? false,
    }),
    /**
     * This function allows us to customize the equality check for the selector.
     * By default, it is a `===` check.
     */
    equalityFn: (prev, next) => {
      // A deep-equal function would probably be more maintainable here, but, we use a shallow one to show that it can be customized.
      if (!next) {
        return false;
      }
      return (
        prev.isBold === next.isBold &&
        prev.isItalic === next.isItalic &&
        prev.isStrike === next.isStrike &&
        prev.isBulletList === next.isBulletList &&
        prev.isOrderedList === next.isOrderedList &&
        prev.isLink === next.isLink
      );
    },
  });

  const handleSetLink = useCallback(
    (previousUrl: string) => {
      if (!editor) return;

      const url = window.prompt('URL', previousUrl);

      // cancelled
      if (url === null) {
        return;
      }

      // empty
      if (url === '') {
        editor.chain().focus().extendMarkRange('link').unsetLink().run();

        return;
      }

      // update link
      editor
        .chain()
        .focus()
        .extendMarkRange('link')
        .setLink({ href: url })
        .run();
    },
    [editor]
  );

  const toggleLink = useCallback(() => {
    if (!editor) return;
    const previousUrl = editor.getAttributes('link').href;

    if (previousUrl) {
      editor.chain().focus().unsetLink().run();
      return;
    }

    handleSetLink(previousUrl);
  }, [editor, handleSetLink]);

  const setLink = useCallback(() => {
    handleSetLink(editor?.getAttributes('link').href);
  }, [editor, handleSetLink]);

  const deleteLink = useCallback(() => {
    if (!editor) return;
    editor.chain().focus().extendMarkRange('link').unsetLink().run();

    return;
  }, [editor]);

  if (!editor) {
    return null;
  }

  return (
    <>
      {currentEditorState && (
        <nav className="mb-1.5">
          <div className="inline-flex -space-x-0 divide-x divide-gray-300 overflow-hidden rounded-lg border border-gray-300 shadow-sm">
            <button
              type="button"
              onClick={() => {
                editor.chain().focus().toggleBold().run();
              }}
              className={`${
                currentEditorState.isBold ? ' bg-slate-400' : 'bg-white'
              } px-3 py-2 text-center text-sm font-medium text-secondary-700 hover:bg-gray-100`}>
              <BoldIcon className="size-4" />
            </button>
            <button
              type="button"
              onClick={() => {
                editor.chain().focus().toggleItalic().run();
              }}
              className={`${
                currentEditorState.isItalic ? ' bg-slate-400' : 'bg-white'
              } px-3 py-2 text-center text-sm font-medium text-secondary-700 hover:bg-gray-100`}>
              <ItalicIcon className="size-4" />
            </button>
          </div>
          <div className="ml-2 inline-flex -space-x-0 divide-x divide-gray-300 overflow-hidden rounded-lg border border-gray-300 shadow-sm">
            <button
              type="button"
              onClick={() => editor.chain().focus().toggleBulletList().run()}
              className={`${
                currentEditorState.isBulletList ? ' bg-slate-400' : 'bg-white'
              } px-3 py-2 text-center text-sm font-medium text-secondary-700 hover:bg-gray-100`}>
              <ListBulletIcon className="size-4" />
            </button>
            <button
              type="button"
              onClick={() => editor.chain().focus().toggleOrderedList().run()}
              className={`${
                currentEditorState.isOrderedList ? ' bg-slate-400' : 'bg-white'
              } px-3 py-2 text-center text-sm font-medium text-secondary-700 hover:bg-gray-100`}>
              <NumberedListIcon className="size-4" />
            </button>
          </div>

          <div className="ml-2 inline-flex -space-x-0 divide-x divide-gray-300 overflow-hidden rounded-lg border border-gray-300 shadow-sm">
            <button
              type="button"
              onClick={toggleLink}
              className={`${
                currentEditorState.isLink ? ' bg-slate-400' : 'bg-white'
              } px-3 py-2 text-center text-sm font-medium text-secondary-700 hover:bg-gray-100`}>
              <LinkIcon className="size-4" />
            </button>
          </div>
        </nav>
      )}
      {editor && (
        <BubbleMenu editor={editor} tippyOptions={{ duration: 100 }}>
          {currentEditorState?.isLink && (
            <BubbleMenuItems
              href={editor.getAttributes('link').href}
              setLink={setLink}
              deleteLink={deleteLink}/>
          )}
        </BubbleMenu>
      )}
      <EditorContent
        editor={editor}
        className="[&_div:focus]:outline-none [&_a]:underline [&_a]:text-primary-800 [&_div]:border [&_div]:block [&_div]:w-full [&_div]:rounded-md [&_div]:p-3 [&_div]:shadow-sm [&_div:focus]:border-primary-300 [&_div:focus]:ring [&_div:focus]:ring-primary-200 [&_div:focus]:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50"/>
      <p className={`mt-1 text-sm  ${editor.storage.characterCount.characters() === limit ? 'text-red-500' : 'text-gray-500'}`}>
        {editor.storage.characterCount.characters()} / {limit} characters
      </p>
    </>
  );
}
