import { createContext, useState } from 'react';

export const RichTextEditorContext = createContext<{
  content: string;
  setContent?: React.Dispatch<React.SetStateAction<string>>
}>({
  content: '',
});

export function RTEProvider({children}: {children: React.ReactNode}) {
  const [ content, setContent ] = useState('');

  return (
    <RichTextEditorContext.Provider value={{content, setContent}}>{children}</RichTextEditorContext.Provider>
  );
}
