  const shadowStyle = validationClasses
    ? isValid
      ? { boxShadow: '0 0 2px 1px rgba(34,197,94, 0.8)' }
      : { boxShadow: '0 0 2px 1px rgba(235,10,10, 0.8)' }
    : undefined;

