type OptionType = {
  id: string | number;
  name: string;
  [key: string]: any | { id: string; name: string };
};

type SelectFnProps = {
  options: Array<OptionType>;
  displayKey?: string;
  valueKey?: string;
} & React.InputHTMLAttributes<HTMLSelectElement>;

type SelectFnRef = React.ForwardedRef<HTMLSelectElement>;

function SelectFn(props: SelectFnProps, ref: SelectFnRef) {
  const { isValid } = useContext(ValidContext);
  const { name, id, options, displayKey, valueKey, className, ...restProps } =
    props;

  const defaultClasses =
    'block w-full rounded-md border-gray-300 shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50';

  let usedClasses = defaultClasses;

  if (className) usedClasses = `${className} ${defaultClasses}`;

  if (!isValid)
    usedClasses = `border-red-300 focus:border-red-300 focus:ring focus:ring-red-200 ${defaultClasses}`;

  return (
    <select
      {...restProps}
      ref={ref}
      name={name}
      id={name ?? id}
      className={usedClasses}>
      <option value="default">Select an option</option>
      {options?.map((item) => {
        let optionValueKey;
        if (valueKey?.includes('.')) {
          let holder = item;
          const split = valueKey.split('.');
          for (const splitElement of split) {
            holder = holder[splitElement];
          }
          optionValueKey = holder;
        }

        return (
          <option
            key={item.id}
            value={optionValueKey ?? item[valueKey ?? 'id']}>
            {displayKey
              ? (item[displayKey] as { name: string }).name
              : item.name}
          </option>
        );
      })}
    </select>
  );
}

type RichTextProps = {
  content?: string;
  limit?: number;
} & React.InputHTMLAttributes<HTMLInputElement>;

type RichtTextRef = React.ForwardedRef<HTMLInputElement>;

function RichTextFn(
  { content, limit, name, id, ...props }: RichTextProps,
  ref: RichtTextRef
) {
  const context = useContext(RichTextEditorContext);

  useEffect(() => {
    if (context.setContent && content) context.setContent(content);
  }, [content]);

  return (
    <>
      <Input.Hidden
        ref={ref}
        name={name}
        id={name ?? id}
        {...props}
        value={context.content}/>
      <RichTextEditor content={content} limit={limit} />
    </>
  );
}

type ValidationFnProps = {
  whenToShow: boolean;
  message: string;
};

function ValidationFn(props: ValidationFnProps) {
  return (
    props.whenToShow && (
      <p className="mt-1 text-sm text-red-500">{props.message}</p>
    )
  );
}

type ValidContextValue = {
  isValid: boolean;
  message?: string;
};

const ValidContext = createContext<ValidContextValue>({
  isValid: true,
  message: '',
});

type ValidationGroupProps = ValidContextValue & React.PropsWithChildren;

function ValidationGroupFn({
  isValid,
  message,
  ...props
}: ValidationGroupProps) {
  const [hasHelpMsgChild, setHasHelpMsgChild] = useState(false);

  useEffect(() => {
    if (Array.isArray(props.children)) {
      for (const child of props.children) {
        if (
          typeof child.type === 'function' &&
          child.type.name === 'ValidationHelpMsgFn'
        ) {
          setHasHelpMsgChild(true);
        }
      }
    }
  }, [props.children]);

  // have a context
  return (
    <ValidContext.Provider value={{ isValid, message }}>
      {props.children}
      {!hasHelpMsgChild && <ValidationHelpMsgFn>{message}</ValidationHelpMsgFn>}
    </ValidContext.Provider>
  );
}
type ValidationHelpMsgProps = React.PropsWithChildren;

function ValidationHelpMsgFn({ children }: ValidationHelpMsgProps) {
  const { isValid, message } = useContext(ValidContext);
  // Use validationgroup context {msg and isValid}
  let output: ReactNode = 'Help Message.';

  if (children) {
    output = children;
  } else if (message) {
    output = message;
  }
  return !isValid && <p className="mt-1 text-sm text-red-500">{output}</p>;
}

export let Input = {
  Text: forwardRef(TextboxFn),
  TextWithValidation: forwardRef(TextboxValidFn),
  TextInlineSelectWithValidation: forwardRef(TextboxInlineSelectValidFn),
  Textarea: forwardRef(TextAreaFn),
  Checkbox: forwardRef(CheckboxFn),
  Select: forwardRef(SelectFn),
  Hidden: forwardRef(HiddenFn),
  CheckboxGroup: forwardRef(CheckboxGroupFn),
  Email: forwardRef(TextboxEmailFn),
  Password: forwardRef(TextboxPasswordFn),
  Radiobox: forwardRef(RadioboxFn),
  RadioboxGroup: forwardRef(RadioboxGroupFn),
  RichText: forwardRef(RichTextFn),
  Validation: forwardRef(ValidationFn), //({children}) => <p>{children}</p>
  ValidationGroup: ValidationGroupFn,
  HelpMsg: ValidationHelpMsgFn,
};
